;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAEzB;;;;;;;IAOI;AAEJ;EACE,oBAAoB;EACpB,eAAe;CAChB;AACD;EACE,oBAAoB;EACpB,gCAAgC;EAChC,mBAAmB;EACnB,oBAAoB;EACpB,eAAe;EACf,iBAAiB;CAClB;AACD;EACE,oBAAoB;EACpB,gCAAgC;EAChC,mBAAmB;EACnB,oBAAoB;EACpB,eAAe;EACf,iBAAiB;CAClB;AACD;EACE,oBAAoB;EACpB,eAAe;CAChB;AACD;EACE,oBAAoB;EACpB,cAAc;CACf;AACD;EACE,oBAAoB;EACpB,eAAe;CAChB;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AACD;;;IAGI;AACJ;EACE,0IAAuG;EAAvG,uGAAuG;EACvG,yBAAiB;EAAjB,iBAAiB;EACjB,2BAA2B;CAC5B;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AACD;;;;IAII;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AACJ;;;;IAII;AACJ;EACE,kCAAkC,CAAC,6CAA6C;CACjF;AACD;EACE,cAAc;CACf;AAED;EACE,cAAc;EACd,0BAA0B;EAC1B,cAAc;CACf;AACD;EACE,sBAAsB;CACvB;AAED;EACE,cAAc;CACf;AC+QD;EACA,mBAAA;EACA,aAAA;EACA,aAAA;CACA;AAEA;EACA,2DAAA;UAAA,mDAAA;CACA;AAEA;AACA;IACA,yBAAA;IACA,aAAA;CACA;AACA;IACA,wBAAA;IACA,cAAA;CACA;CACA;AATA;AACA;IACA,yBAAA;IACA,aAAA;CACA;AACA;IACA,wBAAA;IACA,cAAA;CACA;CACA","file":"ProgressBar.vue","sourcesContent":["/* Seller Progress Bar */\n\n/* #progress .md-card {\n    margin-bottom: 0px;\n    max-width:700px;\n    margin-left: auto;\n    margin-right:auto;\n    border-radius: 10px;\n    background-color:white;\n} */\n\n#progress h1 {\n  font: 28px 'Roboto';\n  color: #29105b;\n}\n#progress h2 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: left;\n}\n#progress h3 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: left;\n}\n#progress h4 {\n  font: 20px 'Roboto';\n  color: #29105b;\n}\n#progress h5 {\n  font: 20px 'Roboto';\n  color:#29105b;\n}\n#progress p {\n  font: 16px \"Roboto\";\n  color: #29105b;\n}\n/* .progress_fill{\n  background: rgb(114,10,196);\n  background: linear-gradient(90deg, rgb(104, 170, 255) 50%, rgb(155, 19, 209) 100%);\n} */\n.progress_fill{\n  background-image: linear-gradient(to right, rgb(180, 28, 147) 0%, #4926b4 51%, rgb(196, 41, 162) 100%);\n  transition: 0.5s;\n  background-size: 200% auto;\n}\n/* .progress_fill{\n  background-image: linear-gradient(to right,#5e30ff 0%, #c444ff 51%, #5e30ff 100%);\n  transition: 0.5s;\n  background-size: 200% auto;\n} */\n/* .progress_fill{\n  background-image: linear-gradient(to right,#4b1afc 0%, #c444ff 51%, #5e30ff 100%);\n  transition: 0.5s;\n  background-size: 200% auto;\n} */\n.progress_fill:hover {\n  background-position: right center; /* change the direction of the change here */\n}\n.responsive-component.small #add-services {\n  padding: 10px;\n}\n\n#progress_modal{\n  color:#301a70;\n  border: 5px #301a70 solid;\n  padding: 30px;\n}\n#publicProfile-link {\n  color: rgb(68, 0, 68);\n}\n\n#publicProfile-link:hover, #publicProfile-link:focus {\n  color: purple;\n}","<template >\n  <div class=\"contrainer\" id=\"progress\" v-if=\"onboarded === false\">\n    <md-card style=\"padding: 20px; margin-bottom: 2%; background-color: white; border-radius: 5px;\">\n      <div class=\"row\">\n        <div class=\"col\">\n          <h4>Profile completion:</h4>\n        </div>\n        <div class=\"col\">\n          <md-button\n            v-if=\"userServices && percentage >= 80 && userServices.length < 5\"\n            class=\"md-raised pull-right\"\n            :md-ripple=\"false\"\n            @click=\"addServices()\"\n          >Improve your profile</md-button>\n          <md-button\n            v-else\n            class=\"md-raised pull-right blinking\"\n            :md-ripple=\"false\"\n            @click=\"determineOnboardingStatus()\"\n          >Complete your profile</md-button>\n          <!-- TO DO: Add link to public profile -->\n          <md-button\n            @click=\"previewPublicProfile()\"\n            id=\"publicProfile-link\"\n            class=\"pull-right\"\n            :md-ripple=\"false\"\n          >Preview your profile</md-button>\n        </div>\n      </div>\n      <md-tooltip md-active>\n        <md-card style=\"background-color: white; color: purple;\">\n          <md-card-content>\n            <h3>To do:</h3>\n            <hr />Add service listing\n            <md-icon v-if=\"userServices && userServices.length >= 1\">check</md-icon>\n            <br />Add about info\n            <md-icon v-if=\"seller && seller.about !== ''\">check</md-icon>\n            <br />Shippo setup\n            <md-icon v-if=\"seller && seller.shippo_api_key !== ''\">check</md-icon>\n            <br />Payout setup\n            <md-icon v-if=\"seller && seller.stripeConnectAcctInfo !== ''\">check</md-icon>\n            <br />\n          </md-card-content>\n        </md-card>\n      </md-tooltip>\n\n      <b-progress :max=\"max\" class=\"mb-3\">\n        <b-progress-bar class=\"progress_fill\" :value=\"percentage\"></b-progress-bar>\n      </b-progress>\n      {{this.percentage}} %\n      <!-- Prompt to commence onboarding-->\n      <modal height=\"auto\" name=\"onboarding-add-services\">\n        <div class=\"container\" id=\"progress_modal\">\n          <h3>To begin marketing, you must add at least 1 service listing.</h3>\n          <br />\n          <h5>A good service listing includes:</h5>\n          <ol>\n            <li>Service description for buyers</li>\n            <li>At least 5 descriptive keywords (the more the better)</li>\n            <li>Pricing</li>\n            <li>Turn-around time</li>\n          </ol>\n          <br />Would you like to add a service?\n          <md-button class=\"md-dense md-raised md-primary\" @click=\"addServices()\">\n            <i class=\"fas fa-atom\" id=\"service_logo\"></i> Add Services\n          </md-button>\n        </div>\n      </modal>\n      <!--  -->\n\n      <modal height=\"auto\" name=\"onboarding-add-stripe-connect\">\n        <div class=\"container\" id=\"progress_modal\">\n          <h3>Add Payout System</h3>\n          <br />\n          <h5>Your payout system is used for buyers to pay you. This information is mandatory before you can recieve payments from buyers.</h5>\n          <br />\n          <md-button\n            class=\"md-dense md-raised md-primary\"\n            @click=\"addPayoutMethod()\"\n          >Add Payout method</md-button>\n          <br />\n          <br />\n          <p>\n            We partner with Stripe to ensure safe & secure transactions.\n            <a>Learn more</a>\n          </p>\n        </div>\n      </modal>\n\n      <modal height=\"auto\" name=\"onboarding-add-shippo-acct\">\n        <div class=\"container\" id=\"progress_modal\">\n          <br />We recommend you use our shipping partners shippo for smoother transactions.\n          <br />With shippo you can handle delicate packages, dry ice, insurance, and get live updates - all on our platform...\n          <br />And you get 30% off!\n          <md-button\n       