mily: 'Roboto', sans-serif; */\n\n\n.about {\n  /* position: relative; */\n  height: 100%;\n  width: 100%;\n  background-image: url(\"HEADER.png\");\n  background-size: cover;\n  /* background-position: center; */\n}\n\n.carousel .container-fluid {\n  max-width: 400px;\n  max-height: 700px;\n  padding-top: 60px;\n\n}\n\n.carousel {\n  width: 100%;\n\n  background-image: radial-gradient(#c6c2ff,70% ,#ffffff); \n  background-size: cover;\n}\n\n.wasp {\n  text-align: center;\n}\n\n\ninput[type=\"text\"] {\n  width: 100%;\n  padding: 12px 20px;\n  margin: 8px;\n  box-sizing: border-box;\n}\n\n\nh1,\nh2 {\n  letter-spacing: 4px;\n  text-transform: uppercase;\n  font-family: \"Open Sans\", sans-serif;\n  font: \"Open Sans\", sans-serif;\n  color: #311c63;\n  text-align: center;\n}\n\nh1 {\n  font: 28px;\n}\n\nh2 {\n  font: 34px;\n}\n\nh3 {\n  letter-spacing: 2px;\n  font-style: medium;\n  font: 26px \"Lato\";\n  color: #311c63;\n  text-align: center;\n}\n\nh4 {\n  font-style: medium;\n  font: 22px \"Lato\";\n  color: #2d1e35;\n  text-align: center;\n}\n\nh5 {\n  font-style: medium;\n  font: 20px \"Roboto\";\n  color: #2d1e35;\n  text-align: center;\n}\n\np {\n  font: \"Open Sans\", sans-serif;\n  font-size: 24px;\n  line-height: 1.5;\n  color: #2d1e35;\n  margin-bottom: 20px;\n  text-align: center;\n}\n\n.center {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 50%;\n  animation-name: moveInLeft;\n  animation-duration: 2s;\n}\n\n#expanded h5 {\n  text-align: center;\n  color: #311c63;\n}\n\n#btn-expand h2{\n  font-size: 30;\n}\n\n#btn-expand h2:hover, #btn-expand h2:focus, #btn-expand h2:active {\n  color: purple;\n  font-size: 30;\n}\n\n/* logo animation */\n@keyframes moveInLeft {\n  0% {\n    opacity: 0;\n    transform: translateX(-100px);\n  }\n\n  80% {\n    transform: translateX(20px);\n  }\n\n  100% {\n    opacity: 1;\n    transform: translate(0);\n  }\n}\n\na {\n  color: #311c63;\n  -webkit-transition: all 0.35s;\n  transition: all 0.35s;\n}\n\na:hover,\na:focus {\n  color: #6a2c86;\n}\n\nhr {\n  max-width: 100px;\n  margin: 25px auto 0;\n  border-width: 1px;\n  border-color: rgba(34, 34, 34, 0.1);\n}\n\nhr.light {\n  border-color: white;\n}\n\n.btn-signup {\n  border-radius: 300px;\n  font-family: \"Roboto\", sans-serif;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-login {\n  border-radius: 300px;\n  font-family: \"Roboto\", sans-serif;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-signup-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-login-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-signup-outline:hover,\n.btn-signup-outline:focus,\n.btn-signup-outline.active {\n  color: white;\n  border-color: #361555;\n  background-color: #361555;\n}\n\n.btn-login-outline:hover,\n.btn-login-outline:focus,\n.btn-login-outline.active {\n  color: white;\n  border-color: #088a12;\n  background-color: #088a12;\n}\n\n.btn-xl {\n  font-size: 11px;\n  padding: 15px 45px;\n}\n\n.icon {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  width: 115px;\n  height: 100px;\n}\n\nsection.about{\n  padding: 150px 0px 150px;\n}\n\nsection.services {\n  padding: 100px 75px 100px;\n}\n\n#pitch {\n  width: 100%;\n  max-width: 600px;\n  height:auto;\n  padding-bottom:0px;\n  padding-top:0px;\n  margin-top:0px;\n}\n\nform {\n  width: 100%;\n  font-size: 14pt;\n  display: inline-block;\n  position: center;\n}\n\n/* ----footer -----*/\n\nfooter {\n  /* margin-top:20px; */\n  padding: 0px 0px 10px;\n  text-align: left;\n  background-color: #260331;\n}\n\nfooter #contact{\n  /* background-color: rgb(190, 190, 190); */\n  background-color:#10021d;\n  /* background-color:#361555; */\n  /* background-image: linear-gradient(#4b1552,#361555); */\n  padding-top: 30px;\n}\n\nfooter #copy-rights {\n  background-color:#260331;\n  padding-top: 20px\n}\n\n#btn-footer{\n  padding:5px;\n  border-radius: 5px;\n}\n\n#contact,\np {\n  font-size: 15px;\n  margin: 0;\n  color:white;\n}\n\n#contact h3 {\n  color: white;\n}\n\n#contact h4 {\n  font-size: 18px;\n  margin: 0;\n  color:white;\n}\n\nfooter .row {\n  padding: 10px;\n}\n\n.bg-primary {\n  background: #482774;\n  background: -webkit-gradient(\n    linear,\n    left top,\n    left bottom,\n    from(#4b1552),\n    to(#250d41)\n  );\n  background: linear-gradient(#4b1552, #250d41);\n}\n\n.text-primary {\n  color: #fdcc52;\n}\n\n.btn-outline {\n  color: rgb(190, 190, 190);\n  border: 1px solid;\n  border-color: rgb(190, 190, 190);\n}\n\n.btn-outline:hover,\n.btn-outline:focus,\n.btn-outline:active,\n.btn-outline.active {\n  color: white;\n  border-color: #361555;\n  background-color: #361555;\n}\n\n.btn {\n  border-radius: 300px;\n  font-family: \"Roboto\", sans-serif;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-xl {\n  font-size: 11px;\n  padding: 15px 45px;\n}\n\n.btn-default {\n  background-color: #361555;\n  color: #fff;\n  border-color: #2f3e48;\n}\n\n/* Header */\nheader.masthead {\n  position: relative;\n  justify-content: center;\n  padding-top: 160px;\n  padding-bottom: 220px;\n  color: white;\n  background: url(\"header_molecules.png\");\n  background: url(\"header_molecules.png\"),\n    -webkit-gradient(linear, right top, left top, from(#c7c7c7), to(#ffffff));\n  background: url(\"header_molecules.png\"), linear-gradient(to right, #e2e2e2, #ffffff);\n  background-size: cover;\n  background-position: center;\n\n}\n\nul {\n  text-align: left;\n}\n\n/* ------Vue Directive Responsive Design Ish ----------------------------------------------------*/\n.responsive-component.small h1,\nh2 {\n  letter-spacing: 1px;\n  font-size: 28px;\n  text-align: center;\n}\n\n.responsive-component.small h3 {\n  letter-spacing: 1px;\n  font-size: 25px;\n  text-align: center;\n}\n\n.responsive-component.small p {\n  width: 100%;\n  font-size: 20px;\n}\n\n.responsive-component.small footer p {\n  width: 100%;\n  font-size: 10px;\n  color:white;\n}\n\n.responsive-component.small form {\n  width: 100%;\n  font-size: 12px;\n}\n\n.responsive-component.small .nav-center {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  margin-top: 10px;\n}\n","<template>\n<div>\n  <body>\n    <br />\n    <br />\n    <br />\n    <br />\n    <br />\n    <br />\n    <br />\n    <br />\n    <br />\n    <br />\n    <md-empty-state\n      md-rounded\n      md-icon=\"access_time\"\n      md-label=\"Coming Soon\"\n      style=\"color: grey; border-style: double;\"\n      class=\"about\"\n    >\n      This place is still under construction. Please come back soon !\n      <img\n        src=\"./LOGO.png\"\n        alt=\"FIG ANALYTICS\"\n        class=\"center\"\n      />\n    </md-empty-state>\n  </body>\n</div>\n</template>\n\n<script>\nexport default {};\n</script>\n\n<style scoped>\n@import \"../assets/css/figanal.css\";\n\n/* @import url('https://fonts.googleapis.com/css?family=Montserrat');  */\n@import url(\"https://fonts.googleapis.com/css?family=Lato|Roboto\");\n/* @import url('https://fonts.googleapis.com/css?family=Open+Sans|Roboto'); */\n</style>\n"],"sourceRoot":""}]);

