lB,mBAAmB;IACnB,aAAa;IACb,aAAa;CAChB;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAED,mEAAmE;AACnE;IACI,eAAe;IACf,gBAAgB;IAChB,mBAAmB;IACnB,WAAW;IACX,YAAY;IACZ,mBAAmB;IACnB,mBAAmB;IACnB,qBAAqB;IACrB,8DAA8C;IAC9C,sDAAsC;IACtC,iCAAiC;IAEjC,yBAAyB;CAC5B;AAED;AACA;;IAEI,8IAAsI;YAAtI,sIAAsI;CACzI;AACD;IACI,4IAAoI;YAApI,oIAAoI;CACvI;AACD;IACI,8IAAsI;YAAtI,sIAAsI;CACzI;AACD;IACI,kJAA0I;YAA1I,0IAA0I;CAC7I;AACD;IACI,gJAAwI;YAAxI,wIAAwI;CAC3I;AACD;IACI,4IAAoI;YAApI,oIAAoI;CACvI;AACD;IACI,kJAA0I;YAA1I,0IAA0I;CAC7I;AACD;IACI,6IAAqI;YAArI,qIAAqI;CACxI;CACA;AACD;AACA;;IAEI,8IAAsI;YAAtI,sIAAsI;CACzI;AACD;IACI,4IAAoI;YAApI,oIAAoI;CACvI;AACD;IACI,8IAAsI;YAAtI,sIAAsI;CACzI;AACD;IACI,kJAA0I;YAA1I,0IAA0I;CAC7I;AACD;IACI,gJAAwI;YAAxI,wIAAwI;CAC3I;AACD;IACI,4IAAoI;YAApI,oIAAoI;CACvI;AACD;IACI,kJAA0I;YAA1I,0IAA0I;CAC7I;AACD;IACI,6IAAqI;YAArI,qIAAqI;CACxI;CACA;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAED,iBAAiB;AAEjB;IACI,oBAAoB;IACpB,eAAe;CAClB","file":"Dashboard.vue","sourcesContent":["body.dashboard {\n  text-align: justify;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  min-height: 100vh;\n  padding: 15px 0px;\n  /* background: linear-gradient(to bottom right, #ffffff 0%, #d8d4dc 100%); */\n  background: linear-gradient(to bottom right, #ffffff 0%, #e1dfe7 100%);\n}\n\n.expand-enter-active,\n.expand-leave-active {\n  transition: height 1s ease-in-out;\n  overflow: hidden;\n}\n\n.expand-enter,\n.expand-leave-to {\n  height: 0;\n}\n\n\n#dashboard #name-location {\n  max-width: 1314px;\n  box-shadow:   2px 2px 5px 1px  #c7c4d1;\n  background: linear-gradient( white 60%, #301a70 40%);\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n  padding-top: 13px;\n  padding-bottom:4px;\n  border-radius: 5px;\n}\n\n#publicProfile-link {\n  color: rgb(68, 0, 68);\n}\n\n#publicProfile-link:hover, #publicProfile-link:focus {\n  color: purple;\n\n}\n\n.dashboard #about {\n  background-color: white;\n  border-width: .5px;\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  box-shadow:   2px 2px 5px 1px  #c7c4d1;\n  border-radius: 5px;\n  text-align: left;\n  padding: 15px;\n}\n\n#dashboard #about .md-content {\n  max-width: 1200px;\n  max-height: 250px;\n  overflow: auto;\n}\n\n#dashboard #stats {\n  border-radius: 5px;\n}\n#services .md-empty-state{\n  color: #bdb7c7;\n}\n\n#stats .card {\n  border-radius: 5px;\n  margin-left:5px;\n  margin-right:5px;\n  box-shadow:   2px 2px 5px 1px  #c7c4d1;\n}\n\n#dashboard .service-border {\n  border-bottom: 2px solid #301a70;\n}\n\n#dashboard .md-checkbox {\n  display: flex;\n}\n\n#dashboard #service-card .md-card-content {\n  max-width: 900px;\n  max-height: 200px;\n  overflow: auto;\n}\n\n#dashboard .md-card {\n  width: 320px;\n  margin: 4px;\n  display: inline-block;\n  vertical-align: top;\n}\n\n\n\n.dashboard #services {\n  padding-left: 20px;\n  padding-right: 20px;\n  padding-top: 0px;\n  background-color: white ;\n  color:black;\n  box-shadow: 1px 1px 2px 1px  #9f9aac;\n  border-radius: 5px;\n}\n\n\n.dashboard #subservice-block {\n  min-width: 95%; \n  margin-left: 40px;\n}\n\n.dashboard .service-block #service-desc{\n  background-color: white;\n  border-radius: 5px;\n}\n\n.dashboard .service-block-sub #service-title{\n  background-color: #301a70;\n  padding-left: 10px;\n  margin-right: 10px;\n  padding-top:0px;\n  border-radius: 5px;\n  box-shadow:0px 0px 0px 0px;\n}\n\n.dashboard .service-block #service-desc{\n  background-color: white;\n  border-radius: 5px;\n}\n\n/* Profile photo - company logo */\n.circle,\n#logo {\n  background-color: white;\n  width: 150px;\n  height: 150px;\n  -moz-border-radius: 75px;\n  -webkit-border-radius: 75px;\n  border-radius: 75px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  /* background-image: url(\"./Icons/noun_DNA_2362091.png\"); */\n  background-image: url(\"./noun_Science_profile_crop_purple.png\");\n}\n\n#dashboard #logo-border {\n  background-color: #301a70;\n  width: 160px;\n  height: 160px;\n  -moz-border-radius: 85px;\n  -webkit-border-radius: 85px;\n  border-radius: 85px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  padding: 5px;\n}\n\n#price-chart {\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n  max-width: 800px;\n}\n\n/*------------- Text ------------------------*/\n\n#dashboard .md-title {\n  font-size: 25px;\n  color: #301a70;\n}\n\n#dashboard .md-title {\n  font-size: 25px;\n  color: #301a70;\n}\n\n#dashboard #service_logo {\n  color: #301a70;\n  font-size: 25px;\n}\n\n.dashboard #dashboard h1 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 35px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.dashboard #dashboard h2 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: left;\n}\n\n.dashboard #dashboard h3 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.dashboard #services h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n\n.pricing_block h5{\n  font: 16px \"Roboto\";\n  color: rgb(100, 100, 100);\n} \n\n.sub_pricing_block h5{\n  font: 16px \"Roboto\";\n  color: rgb(100, 100, 100);\n} \n\n.dashboard .service-block h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.dashboard .service-block-sub h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n#dashboard #about h4 {\n  /* font-style: medium; */\n  font: 19px \"Roboto\";\n  color: black;\n  text-align: left;\n}\n\n#dashboard #about p {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 17px \"Roboto\";\n  color: black;\n  text-align: center;\n\n}\n\n.dashboard #dashboard #stats h4 {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 18px \"Roboto\";\n  text-align: center;\n}\n.dashboard #time {\n  color:blue;\n  font-size: 18px;\n  padding:3px;\n}\n\n.dashboard #negTime {\n  color: blue;\n  background-color: rgb(239, 239, 253);\n  border: blue 3px solid; \n  border-radius: 5px;\n  padding:3px;\n}\n.dashboard #price {\n  color:#3f9e00;\n  font-size: 18px;\n  padding:3px;\n  text-align:right;\n}\n\n.dashboard #negPrice {\n  color: #3f9e00;\n  background-color: #eeffe2;\n  border: #3f9e00 3px solid; \n  border-radius: 5px;\n  padding:3px;\n  text-align:right;\n}\n.dashboard #subTime {\n  color:blue;\n  font-size: 17px;\n  padding:3px;\n}\n\n.dashboard #subNegTime {\n  color: blue;\n  background-color: rgb(239, 239, 253);\n  border: blue 3px solid; \n  border-radius: 5px;\n  padding:3px;\n  font-size: 14px;\n}\n.dashboard #subPrice {\n  color:#3f9e00;\n  font-size: 17px;\n  padding:3px;\n}\n\n.dashboard #subNegPrice {\n  color: #3f9e00;\n  background-color: #eeffe2;\n  border: #3f9e00 3px solid; \n  border-radius: 5px;\n  padding:3px;\n  font-size: 14px;\n}\n\n#dashboard #service-desc h5 {\n  font: 15px \"Roboto\";\n  color: #111;\n}\n\n#dashboard #services h6 {\n  font: 17px \"Roboto\";\n  color: white;\n}\n\n#dashboard #stats h6 {\n  font: 15px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n.card-text {\n  font: 18px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#dashboard p {\n  font: 20px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#dashboard #price-chart p  {\n  font: 16px \"Roboto\";\n  color: #111;\n}\n\n#dashboard #spec_inquiry p {\n  text-align: left;\n}\n\n.md-checkbox {\n  display: flex;\n}\n\n#add_service{\n  padding-top:10px;\n}\n\n#service-form-block {\n  color:#301a70;\n  padding: 30px;\n}\n\n.pricing_block {\n  background-color: #f8f6fa;\n  padding-left: 10px;\n  margin-right: -10px;\n  margin-left: -20px;\n}\n\n.sub_pricing_block{\n  background-color: #f8f6fa;\n  padding-left: 15px;\n  margin-right: -15px;\n  margin-left: -15px;\n}\n\n#service-form-block table{\n  width: 100%;\n  table-layout: fixed;\n  background: #e2ddeb;\n  margin-left: -10px;\n  padding-left: 20px;\n\n}\n\n#subservice-form-block table {\n  width: 100%;\n  table-layout: fixed;\n  background: #e2ddeb;\n  margin-left: -15px;\n  padding-left: 20px;\n  margin-right: -30px;\n}\n\n#subservice-form-block {\n  color:#301a70;\n  padding-right: -15px;\n}\n\n\n/* Responsive Text */\n\na {\n  color: #311c63;\n  -webkit-transition: all 0.35s;\n  transition: all 0.35s;\n}\n\na:hover,\na:focus {\n  color: #6a2c86;\n}\n\nhr {\n  max-width: 100px;\n  margin: 25px auto 0;\n  border-width: 1px;\n  border-color: rgba(34, 34, 34, 0.1);\n}\n\np {\n  font-size: 18px;\n  line-height: 1.5;\n  /* color: #dad8d8; */\n  margin-bottom: 20px;\n}\n\n.form p {\n  font-size: 12px;\n  line-height: 1.5;\n  /* color: #dad8d8; */\n}\n\n.expand-enter-active,\n.expand-leave-active {\n  transition-property: opacity, height;\n}\n\n.expand-enter,\n.expand-leave-to {\n  opacity: 0;\n}\n\n/