cation {\n  max-width: 1314px;\n  box-shadow:   2px 2px 5px 1px  #c7c4d1;\n  background: linear-gradient( white 60%, #301a70 40%);\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n  padding-top: 13px;\n  padding-bottom:4px;\n  border-radius: 5px;\n}\n.public-profile #about {\n  background-color: white;\n  border-width: .5px;\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  box-shadow:   2px 2px 5px 1px  #c7c4d1;\n  border-radius: 5px;\n  text-align: left;\n  padding: 15px;\n}\n.public-profile #about .md-content {\n  max-width: 1200px;\n  max-height: 250px;\n  overflow: auto;\n}\n\n#public-profile #service-description {\n  max-width: 1200px;\n  max-height: 200px;\n  overflow: auto;\n  margin-bottom: 5px;\n}\n\n#public-profile #stats {\n  border-radius: 5px;\n}\n\n\n\n.public-profile #services {\n  padding-left: 20px;\n  padding-right: 20px;\n  padding-top: 0px;\n  background-color: white ;\n  color:black;\n  box-shadow: 1px 1px 2px 1px  #9f9aac;\n  border-radius: 5px;\n}\n\n#public-profile .service-border {\n  border-bottom: 2px solid #301a70;\n}\n\n.public-profile #subservice-block {\n  min-width: 95%; \n  margin-left: 40px;\n}\n\n.public-profile .service-block #service-desc{\n  background-color: white;\n  border-radius: 5px;\n}\n\n.public-profile .service-block-sub #service-title{\n  background-color: #301a70;\n  padding-left: 10px;\n  margin-right: 10px;\n  padding-top:0px;\n  border-radius: 5px;\n  box-shadow:0px 0px 0px 0px;\n}\n\n.public-profile .service-block #service-desc{\n  background-color: white;\n  border-radius: 5px;\n}\n#inquiry-modal{\n  background-color: transparent;\n}\n#gen_inquiry {\n  color:#301a70;\n  padding: 30px;\n}\n#spec_inquiry{\n  color:#301a70;\n  border: 5px #301a70 solid;\n  padding: 30px;\n}\n\n\n/* Profile photo - company logo */\n.circle,\n#logo {\n  background-color: white;\n  width: 150px;\n  height: 150px;\n  -moz-border-radius: 75px;\n  -webkit-border-radius: 75px;\n  border-radius: 75px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-image: url(\"./noun_Science_profile_crop_purple.png\");\n}\n\n#public-profile #logo-border {\n  background-color: #301a70;\n  width: 160px;\n  height: 160px;\n  -moz-border-radius: 85px;\n  -webkit-border-radius: 85px;\n  border-radius: 85px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  padding: 5px;\n}\n\n/* #price-chart {\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n  max-width: 800px;\n} */\n\n.public-profile .submit-buttons-md {\n  background-color: #2238ff;\n  color: white;\n}\n.public-profile div.card {\n  margin-left: auto;\n  margin-right: auto;\n}\n.public-profile #shopping-cart {\n  margin-left: 1000px;\n  border: 1px solid green;\n}\n.public-profile #request-quote-cart {\n  margin-left: 1000px;\n  border: 1px solid green;\n}\n\n/* TEXT */\n\n#public-profile .md-title {\n  font-size: 25px;\n  color: #4527a0;\n}\n\n#public-profile .md-title {\n  font-size: 25px;\n  color: #4527a0;\n}\n\n#public-profile #service_logo {\n  color: #4527a0;\n  font-size: 25px;\n}\n\n.public-profile #public-profile h1 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 35px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.public-profile #public-profile h2 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: left;\n}\n\n.public-profile #public-profile h3 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.public-profile #services h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.public-profile .service-block h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.public-profile .service-block-sub h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n#public-profile #about h4 {\n  /* font-style: medium; */\n  font: 19px \"Roboto\";\n  color: black;\n  text-align: left;\n}\n#public-profile #about p {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 17px \"Roboto\";\n  color: black;\n  text-align: center;\n}\n.public-profile #public-profile #stats h4 {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 18px \"Roboto\";\n  text-align: center;\n}\n#public-profile #service-desc h5 {\n  font: 15px \"Roboto\";\n  color: #111;\n}\n#public-profile #services h6 {\n  font: 17px \"Roboto\";\n  color: white;\n}\n#public-profile #stats h6 {\n  font: 15px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n.card-text {\n  font: 18px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#public-profile p {\n  font: 20px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#public-profile #price-chart p  {\n  font: 16px \"Roboto\";\n  color: #111;\n}\n\n.md-checkbox {\n  display: flex;\n}\n\n#add_service{\n  padding-top:10px;\n}\n\n.public-profile #time {\n  color:blue;\n  font-size: 18px;\n  padding:2px;\n}\n\n.public-profile #negTime {\n  color: blue;\n  background-color: rgb(239, 239, 253);\n  border: blue 2px solid; \n  border-radius: 5px;\n  padding:3px;\n}\n.public-profile #price-container {\n  width: 200px;\n  min-width:200px;         \n  width: auto !important;\n  text-align:right;\n}\n.public-profile #btn-container {\n  text-align: right;\n  margin-bottom: 3px;\n}\n.public-profile #price {\n  color:#3f9e00;\n  font-size: 18px;\n  padding:2px;\n}\n\n.public-profile #negPrice {\n  color: #3f9e00;\n  background-color: #eeffe2;\n  border: #3f9e00 3px solid; \n  border-radius: 5px;\n  padding:2px;\n  margin-bottom:2px;\n}\n#service-form-block {\n  color:#4527a0 #44ceab;\n  border: 5px #4527a0 solid;\n  padding: 30px;\n}\n\n#service-form-block table {\n  width: 100%;\n  table-layout: fixed;\n  background: #ebe8f1;\n}\n\n#subservice-form-block {\n  colo